import { Component, OnInit } from '@angular/core';
import { FormBuilder, FormGroup, FormControl, Validators } from '@angular/forms';
import { Router } from '@angular/router';

// SERVICE
import { UserService } from 'src/app/shared/service/user.service';

// MODEL
import { User } from 'src/app/shared/models/user.model';

@Component({
  selector: 'app-login',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  public loginForm: FormGroup;
  public errorMessage: string;
  public isSubmitted = false;
  public currentUser: User;

  constructor(
    private fb: FormBuilder,
    private router: Router,
    private userService: UserService
  ) { }

  ngOnInit() {

    this.loginForm = this.fb.group({
      email: [ '', [Validators.required]],
      password: ['', [Validators.required]]
    });
  }

  public submit(): void {
    this.isSubmitted = true;
    console.log(this.loginForm.value);
    this.userService.login(this.loginForm.value).subscribe( () => {
      this.userService.getCurrentUser().subscribe(
        currentUser => {
          this.currentUser = currentUser;
          this.router.navigate(['accueil', 'post', this.currentUser._id ]);
        });
    }, err => {
      console.log(err);
      if (err.status === 401) {
        this.errorMessage = 'Ce compte n\'existe pas. Veuillez crer un nouveau compte';
      } else {
        this.errorMessage = 'Une erreur c\'est produite. impossible d\'accder  votre compte';
      }
    });
  }

}
